.header-bottom__status {
    height: 100%;
    display: flex;
    align-items: center;

    .status__btn {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-right: 32px;
        font-size: 16px;
        transition: color .3s ease-in-out;
        color: var(--color-mine-shaft);

        &:hover {
            transition: color .3s ease-in-out;
            color: var(--color-matisse);
        }

        &:hover  svg{
            transition: stroke .3s ease-in-out;
            stroke: var(--color-matisse);
        }

        svg {
            display: inline-block;
            width: 25px;
            margin-bottom: 6px;
            height: 25px;
            transition: stroke .3s ease-in-out;
            stroke: var(--color-tundora);
        }
    }

    .cart__btn {
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: 16px;
        letter-spacing: 0.002em;
        transition: color .3s ease-in-out;
        color: var(--color-mine-shaft);

        &:hover {
            transition: color .3s ease-in-out;
            color: var(--color-matisse);
        }

        &:hover  svg{
            transition: stroke .3s ease-in-out;
            stroke: var(--color-matisse);
        }

        svg {
            display: inline-block;
            width: 24px;
            margin-bottom: 6px;
            height: 24px;
            transition: stroke .3s ease-in-out;
            stroke: var(--color-tundora);
        }

        .cart__count {
            display: none;
        }

    }
}

// cart-display
.cart-display {
    position: relative;
    height: 72px;
    display: flex;
    margin-bottom: 24px;
    justify-content: space-between;
    padding: 16px 66px 11px 16px;
    border-radius: var(--border-radius);
    background-color: var(--color-white);
    box-shadow: -1px 4px 13px rgba(0, 0, 0, 0.05);

    &__status {
        display: flex;
        flex-direction: column;
        font-weight: 500;
        font-size: 14px;
    }

    &-count {
        display: inline-block;
        margin-bottom: 7px;
        color: var(--color-matisse);
    }

    &__close {
        width: 20px;
        height: 24px;
        cursor: pointer;
        position: absolute;
        right: 10px;
        top: 21px;

        &::before,
        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 2px;
            transform: rotate(45deg);
            background-color: var(--color-matisse);
        }

        &::after {
            bottom: 0;
            transform: rotate(-45deg);
        }
    }
}
