.header {

  &-top {
      padding-top: 15px;
      padding-bottom: 13px;
      background-color: var(--color-mine-shaft);

      &__container {
          display: flex;
          align-items: center;
          justify-content: space-between;
      }

      &__right {
          width: 75%;
          display: flex;
          align-items: center;
          justify-content: space-between;
      }
  }


  &__location {
      svg {
          display: inline-block;
          margin-right: 4px;
          width: 10px;
          height: 10px;
          transition: fill .3s ease-in-out;
          fill: #FAFBFC;
      }

      &:hover svg {
          transition: fill .3s ease-in-out;
          fill: var(--color-bright-sun);
      }

      &:hover .location__city {
          transition: color .3s ease-in-out;
          color: var(--color-bright-sun);
      }

      &:hover .location__city::before {
        transition: background-color .3s ease-in-out;
        background-color: var(--color-bright-sun);
      }

      .location__city {
          position: relative;
          font-weight: 500;
          transition: color .3s ease-in-out;
          color: var(--color-athens-gray);

          &::before {
            @include ghost;
            height: 1px;
            top: initial;
            bottom: 1px;
            transition: background-color .3s ease-in-out;
            background-color: var(--color-athens-gray);
          }
      }
  }

  &__number {
      font-size: 18px;
      font-weight: 500;
      transition: color .3s ease-in-out;
      color: var(--color-athens-gray);

      &:hover {
          transition: color .3s ease-in-out;
          color: var(--color-bright-sun);
      }

      svg {
          display: inline-block;
          margin-right: 5px;
          width: 13px;
          height: 13px;
          transition: stroke .3s ease-in-out;
          stroke: var(--color-white);
      }

      &:hover svg {
          transition: stroke .3s ease-in-out;
          stroke: var(--color-bright-sun);
      }

  }

  .header-bottom {
      padding-top: 9px;
      padding-bottom: 10px;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04), 0px 2px 6px rgba(0, 0, 0, 0.04), 0px 0px 1px rgba(0, 0, 0, 0.04);

      &__container {
          display: flex;
          align-items: center;
          justify-content: space-between;
      }

      .header__logo {
          padding-top: 10px;
      }

      &__right {
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: space-between;
          width: 75%;
      }

      &__form {
          padding: 2px;
          width: 75%;
          border: 1px solid var(--color-matisse);
          border-radius: var(--border-radius);
          display: flex;
          align-items: center;
      }

      &__input {
          width: 100%;
          height: 37px;
          padding-left: 12px;
          border: none;
      }

  }

}
