.hits {
    padding-top: 24px;
    padding-bottom: 34px;

    &__heading {
        margin: 0;
        margin-bottom: 41px;
    }

    &__content {
      padding: 7px 5px 13px 3px;
      overflow: hidden;
    }

    &__slider {
      position: relative;
      height: 100%;
    }

    &-slide {
      // max-width: 254px;
      position: relative;
      padding-top: 24px;
      padding-bottom: 32px;
      text-align: center;
      background-color: var(--color-white);
      height: 100%;
      transition: color .3s ease-in-out;
      box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
      border-radius: var(--border-radius);

      &:hover .hits-slide__name {
        transition: color .3s ease-in-out;
        color: var(--color-sunset-orange);
      }

      img {
        margin-bottom: 20px;
      }

      &__inner {
        padding: 0 16px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;
      }

      &__name {
        display: block;
        margin-bottom: 24px;
        text-align: start;
        font-size: 18px;
        line-height: 24px;
        transition: color .3s ease-in-out;
        color: var(--color-tundora);

        &::before {
          @include ghost;
        }

        &:hover {
          transition: color .3s ease-in-out;
          color: var(--color-mandy);
        }
      }

      &__vendor {
        display: block;
        margin-bottom: 35px;
        font-size: 14px;
        color: var(--color-boulder);
      }

      &__price {
        font-size: 20px;
        font-weight: 700;
        color: var(--color-mine-shaft);
      }
    }
}
