.hero {
    padding-bottom: 25px;

    &__content {
        padding-top: 24px;
        overflow: hidden;
        border-radius: var(--border-radius);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
    }

    &__heading {
        margin: 0;
        font-family: var(--font-family-raleway);
        font-weight: 500;
        font-size: 55px;
        letter-spacing: 1px;
        color: var(--color-mine-shaft);
    }
}
