.nav {

    &__list {
        display: flex;
        align-items: center;
    }

    &__item:not(:last-child) {
        margin-right: 52px;
    }

    &__link {
        font-weight: 500;
        transition: color .3s ease-in-out;
        color: var(--color-athens-gray);

        &:hover {
            transition: color .3s ease-in-out;
            color: var(--color-bright-sun);
        }
    }

}
