.footer {
  padding-top: 50px;
  padding-bottom: 38px;
  background-color: var(--color-mine-shaft);

  &-top {
    display: flex;
    padding-bottom: 26px;
    border-bottom: 1px solid rgba(205, 203, 203, 0.2);

    &__block {
      min-height: 267px;
      width: calc(30% - 85px);
      padding-left: 16px;
      border-left: 1px solid rgba(205, 203, 203, 0.2);

      &--logo {
        border: none;
        padding: 0;
      }

      &--shop {
        margin-right: 19px;
      }

      &--company {
        margin-right: 17px;
      }

      &--contacts {
        .footer-top__item {
          margin-bottom: 20px;
        }
      }
    }

    &__title {
      margin: 0;
      margin-bottom: 22px;
      font-weight: 500;
      font-size: 18px;
      color: var(--color-athens-gray);
    }

    &__item {
      margin-bottom: 8px;
    }

    &__link {
      color: var(--color-alto);
      transition: color .3s ease-in-out;

      &:hover {
        transition: color .3s ease-in-out;
        color: var(--color-golden-tainoi);
      }
    }

    &__subtitle {
      margin-bottom: 8px;
      display: block;
      color: var(--color-silve);
    }

    &__contacts {
      padding-left: 24px;
      font-weight: 500;
      font-size: 18px;
      background-position: left;
      background-repeat: no-repeat;
      color: var(--color-athens-gray);

      &--address {
        background-image: url("../img/marker.svg");
      }

      &--phone {
        background-image: url("../img/phone.svg");
      }

      &--email {
        background-image: url("../img/mail.svg");
      }
    }
  }

  &-bottom {
    &__inner {
      display: flex;
      padding-top: 24px;
      padding-bottom: 24px;
      border-bottom: 1px solid rgba(205, 203, 203, 0.2);
    }

    &__block {
      width: 50%;
      display: flex;
      align-items: center;

      &:first-child {
        margin-right: 18px;
      }
    }

    &__title {
      margin: 0;
      margin-right: 47px;
      font-weight: 400;
      font-size: 16px;
      color: var(--color-athens-gray);
    }

    &__list {
      display: flex;
      align-items: center;
    }

    &__item {
      width: 70px;
      height: 48px;
      border-radius: 6px;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;

      &:not(:last-child) {
        margin-right: 16px;
      }

      &--apple {
        background-image: url("../img/apple.svg");
      }

      &--google {
        background-image: url("../img/google.svg");
      }

      &--master-card {
        background-image: url("../img/master-card.svg");
      }

      &--visa {
        background-image: url("../img/visa.svg");
      }
    }

    &__block--social {
      .footer-bottom__title {
        margin-right: 32px;
      }

      .footer-bottom__item {
        width: 48px;

        &:not(:last-child) {
          margin-right: 16px;
        }
      }
    }

    &__link {
      display: inline-block;
      width: 100%;
      height: 48px;
      filter: grayscale(35%);
      background-position: center;
      background-repeat: no-repeat;
      transition: filter .3s ease-in-out;
      background-size: cover;

      &:hover {
        transition: filter .3s ease-in-out;
        filter: grayscale(0);
      }

      &--vk {
        background-image: url("../img/vk.svg");
      }

      &--fb {
        background-image: url("../img/fb.svg");
      }

      &--insta {
        background-image: url("../img/insta.svg");
      }

      &--you-tube {
        background-image: url("../img/you-tube.svg");
      }
    }

    &__copyright {
      margin: 0;
      padding-top: 24px;
      font-size: 12px;
      color: var(--color-gallery);
    }
  }
}
